import { LitElement, html } from 'lit';
import { customElement } from 'lit/decorators.js';

// You can also import styles from another file
// if you prefer to keep your CSS seperate from your component

import { styles as sharedStyles } from '../../../styles/shared-styles';

import '@shoelace-style/shoelace/dist/components/breadcrumb/breadcrumb.js';
import '@shoelace-style/shoelace/dist/components/breadcrumb-item/breadcrumb-item.js';

import '@shoelace-style/shoelace/dist/components/card/card.js';
import '@shoelace-style/shoelace/dist/components/drawer/drawer.js';
import { resolveRouterPath } from '../../../router';

@customElement('kitab-al-tahara-bab-al-wodo')
export class AppAbout extends LitElement {
  static styles = [sharedStyles];

  render() {
    return html`
      <app-header ?enableBack="${true}"></app-header>

      <main>
        <sl-breadcrumb>
          <sl-breadcrumb-item>
            <sl-icon slot="prefix" name="house"></sl-icon>
            الصفحة الرئيسية
          </sl-breadcrumb-item>
          <sl-breadcrumb-item>كتاب الطهارة</sl-breadcrumb-item>
          <sl-breadcrumb-item>باب الوضوء</sl-breadcrumb-item>
        </sl-breadcrumb>
        <h1>باب الوضوء</h1>

        <sl-button-group label="Alignment">
          <sl-button
            size="large"
            href="${resolveRouterPath('kitab-al-tahara/bab-al-wodo/1')}"
          >
            .تَبْلُغُ الحِلْيَةُ مِنَ المُؤْمِنِ، حَيْثُ يَبْلُغُ
            الوَضُوءُ</sl-button
          >
        </sl-button-group>
      </main>
    `;
  }
}
